

.header {
	
	width: @hyb-device-width;
	max-width: @hyb-device-width;
	height: @header-size;

	background: @header-color;
	z-index: 1;

	box-shadow: 0px -4px 8px rgba(0, 0, 0, 0.4);

	&__inner {
		display: flex;
		height: 100%;
	}

	// Touch device fix:
	// Prevent the touch device zooming in from doubletaps on the header.
	touch-action: manipulation;
	
	// Controls
	// --------------------------------------------------
	&__controls-container {
		position: relative;

		flex-grow: 0;
		flex-shrink: 0;

		width: @nav-size;

		&::after {
			content: "";
			position: absolute;

			right: 0;
			top: @padding-ver;
			bottom: @padding-ver;

			border-right: 1px solid fade(@header-color-inverted2, 25%);
		}
	}

	&__menu-btn {
		display: flex;
		align-items: center;
		justify-content: center;
		width: 100%;
		height: 100%;

		padding: @padding-ver @padding-hoz;

		font-size: @title-size;
		color: @header-color-inverted;

		background-color: transparent;

		.no-touch &:hover {
			background: darken(@header-color, 2.5%);
		}
	}

	&__menu-btn-icon {
		margin-right: 0.25em;
	}

	&__menu-btn-icon::before {
		font-size: @title-size*1.4;
		color: @header-color-inverted2;
	}
	
	// Body
	// --------------------------------------------------
	&__body {
		flex-grow: 1;
		flex-shrink: 1;
		padding: /*@padding-ver*/ 0 @padding-hoz;

		display: flex;
		align-items: center;

		line-height: @header-line-height;
	}

	&__body-inner {
		position: relative;

		// Slightly increases header height (and by proxy, the logo height).
		//padding-top: @padding-ver / 2;
		//padding-bottom: @padding-ver / 2;
	}

	&__title {
		font-size: @title-size;
		color: @header-color-inverted;
	}

	// Rare: JavaScript will try to scaledown the title to fit it on one line.
	&__title.fit-1 {font-size: @title-size - 0.0625rem;}
	&__title.fit-2 {font-size: @title-size - 0.125rem;}

	&__sub-title {
		font-size: @subtitle-size;
		color: @header-color-inverted2;

		margin-top: @subtitle-margin;
	}


	// Logo
	// --------------------------------------------------
	&__logo {

		flex-grow: 0;
		flex-shrink: 0;

		//position: absolute;
		height: 100%;
		//top: 0;
		//right: 0;

		flex-grow: 0;
		flex-shrink: 0;

		padding: @padding-ver @padding-hoz;

		img {
			min-width: 0;
			max-width: auto;
			height: 100%;
		}
	}
}