// Font face
// ---------------------------------------------------------------------
.fontFace(@family; @file; @weight; @style) {
    
    @filename: "fonts/@{family}/@{file}";
    @font-face {
        font-family: @family;
        src: url("@{filename}.eot");
        src: url("@{filename}.eot?#iefix") format('embedded-opentype'),
             url("@{filename}.woff2") format('woff2'),
             url("@{filename}.woff") format('woff'),
             url("@{filename}.ttf") format('truetype');
        font-weight: @weight;
        font-style: @style;
    }
}

.fontFace("Arimo"; "Arimo-Regular";       400; normal);
.fontFace("Arimo"; "Arimo-Italic";        400; italic);
.fontFace("Arimo"; "Arimo-Medium";        500; normal);
.fontFace("Arimo"; "Arimo-MediumItalic";  500; italic);
.fontFace("Arimo"; "Arimo-Bold";          700; normal);
.fontFace("Arimo"; "Arimo-BoldItalic";    700; italic);

.font-smoothing() {
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}


// Font variables
// ---------------------------------------------------------------------

@body-family: 'Arimo', Helvetica, Arial, sans-serif;

@body-size: 	1rem;
@btn-size: 		1rem;

@header-line-height: 1.2; // Reduced line-height in the header to allow for 2 lines of subtitle.

@title-size: 	1.75rem;
@subtitle-size: 1.25rem;
@subtitle-margin: 0.375rem; //0.25rem;


@menu-subtitle-margin: 1.25rem;

@item-title-size: @body-size;
@item-title-margin: @body-size/2;

@progress-preview-title-size: @body-size * 0.75;

body {
	.font-smoothing;
	font-family: @body-family;
	font-size: @body-size;
	font-weight: normal;
	line-height: 1.5;
	color: black;
}

p {
    &:first-child {
        margin-top: 0;
    }
    &:last-child {
        margin-bottom: 0;
    }
}