.mc {
	
	color: @menu-color-inverted;
	height: 100%;

	&__item {
		padding: @item-padding;

		&:not(:first-child) {
			border-top: 1px solid fade(@menu-color-inverted, 20%);
		}
	}

	&__item-title {
		font-size: @item-title-size;
		font-weight: bold;

		margin-bottom: @item-title-margin;
	}

	&__item-body {

	}

	// References
	// ------------------------------------------------------
	// Remove the icon from references (icon is added by the core Hybrid functionality).
	// (Stylistic choice).
	&-references__item {

		// Hyperlinks can be quite long. Allow them to break across multiple lines.
		.hyperlink__content {
			word-break: break-all;
		}

		.hyperlink .icon {
			display: none;
		}
	}

	// States
	// ------------------------------------------------------
	display: none;
	&.is-active {
		display: block;
	}
}