// Global <img> tag rules
// ---------------------------------------------

@broken-img-color: #DDDDDD;
@broken-img-color-inverted: mix(#DDDDDD, #555555, 30%);
@border-width: 2/16rem;

img {

	position: relative;

	// Broken image message
	// -------------------------------------------
  	&::before,
	&::after {
		content: "";
		position: absolute;
		
		top: 50%;
		right: 0;
		left: 0;
    	transform: translateY(-50%);
	}
	
  	// Background.
	&::before { 
		background-color: @broken-img-color;
		border: @border-width*2 dashed @broken-img-color-inverted;
    	min-height: @item-padding * 4;
		height: 100%;
	}

  	// Icon
	&::after { 
	
		content: "\e9ef";
		font-family: "vanilla";
		line-height: 0.8em;
	
		position: absolute;
		font-size: @item-title-size * 1.2;
		font-weight: bold;
		color: @broken-img-color-inverted;
		text-align: center;
		
		width: auto;
		height: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
	}
}