// Scrollbar styles
// ---------------------------------------------
// Stylize the scrollbar, and have scrollbars be visible on touch devices.

// Disable pull-to-refresh.
body {
  overscroll-behavior: contain;
}

@scrollbar-thumb-color: 		mix(@menu-color, @menu-color-inverted, 25%);
@scrollbar-track-color: 		fade(@scrollbar-thumb-color, 40%);

// FIXME: Copied from src/extensions/adapt-scrollfix/less/scrollfix.less
[data-scroll] {
	overflow: auto;
	overflow-x: hidden;
	height: 100%;
}

// Once simplebar is setup, hide the default HTML scrollbar.
.has-simplebar {
    overflow: visible !important;
	overflow-x: hidden !important;
}
.simplebar-content-wrapper[style*="overflow: hidden scroll;"] {
    padding-right: 20px;          // Add in the same padding that would be there if there was a normal HTML scrollbar.
}

.simplebar-track::before {
    content: "";
    position: absolute;
    inset: 2px 3px;
    background: @scrollbar-track-color;
    border-radius: 5px;
}

.simplebar-scrollbar::before {
    background: @scrollbar-thumb-color !important;
    opacity: 1 !important;
}

// Notes styles
.notes .simplebar-track::before {
    background: @notes-scrollbar-track-color;
}
.notes .simplebar-scrollbar::before {
    background: @notes-scrollbar-thumb-color !important;
}