// Outer wrapper
// --------------------------------------------------------------------

html, body {
	overflow: hidden;
}

body {
	background: @background-color;
}
	
// FIXME: Grunt needs to be set up to not auto-generate the .loading element anymore.
.loading {
	display: none !important;
}

// Negative space behind the menu.
.app {
	position: relative;
	height: 100vh;
	width: 100vw;
	display: block;

	overflow: hidden;
	transform: translateZ(0);
}

.menu-loading {

	position: fixed;
	inset: 0;
	display: flex;
	align-items: center;
	justify-content: center;

	color: @background-color-inverted;
	font-size: @title-size;

	@keyframes anim-ddloading-idle {
		0% {opacity: 0.1;}
		30% {opacity: 1;}
		70% {opacity: 1;}
		100% {opacity: 0.1;}
	}
	animation: anim-ddloading-idle @duration*12 ease infinite normal;
}

.wrapper {
	
	// The wrapper will be absolutely centered in the middle of the viewport with JavaScript.
	position: absolute;
	transform-origin: 0 0;
	padding-top: 0 !important;
	//overflow: hidden !important;
	border-radius: @border-radius;

	&__inner {
		display: flex;
		flex-direction: column;

		& > * {
			position: relative;
		}
	}
}

#content {
	overflow: hidden;
}