.notes {

	position: absolute;
	width: @notes-width;
	height: @notes-height;
	background: @notes-color;

	border-top-left-radius: @border-radius;
	border-top-right-radius: @border-radius;
	overflow: hidden;

	box-shadow: 0px 2px 3px rgba(0, 0, 0, 0.4);

	// BUGFIX: Prevent iOS devices from auto-focusing on the element when it's not active!
	pointer-events: none; 

	bottom: 0;
	right: 0;

	&__inner {
		display: flex;
		flex-direction: column;
		height: 100%;
	}

	// States
	// ---------------------------------------------------------
	.transition(transform @duration*3 ease);
	transform: translateY(105%);
	&.is-active {
		transform: translateY(0);
		pointer-events: all; 
	}

	// Handle
	// ---------------------------------------------------------
	&__handle {
		background: @notes-header-color;
		color: @notes-header-color-inverted;

		padding: @notes-padding;
		box-shadow: 0px 2px 3px rgba(0, 0, 0, 0.2);

		cursor: pointer;
		.u-no-select;

		flex-grow: 0;
		flex-shrink: 0;

		z-index: 1;
	}

	&__handle-title {

		.icon {
			&::before {
				font-size: 1.25em;
				display: inline;
			}
		}
	}

	// Content
	// ---------------------------------------------------------
	&__content {
		flex-grow: 1;
		overflow: auto;
	}

	// Items
	// ---------------------------------------------------------

	&__item {
		padding: @item-padding;

		&.is-active {
			background: rgba(255,255,255,0.35);
		}
	}

	&__item-inner {
		display: flex;
		flex-direction: column;
	}

	&__item-title {
		font-weight: bold;
		margin-bottom: @item-margin;
	}

	&__item-textarea {

		overflow: hidden;
		overflow-x: hidden;
		overflow-wrap: break-word;

		outline: none;
		resize: none;
		background: transparent;
		cursor: text;
		border: none;

		font-family: inherit;
		font-size: inherit;

		&::placeholder {
			font-style: italic;
		}
	}

	// Controls
	// ---------------------------------------------------------
	&__controls-container {
		padding: @notes-padding;
		background: mix(@notes-color, @notes-header-color, 90%);

		box-shadow: 0px -2px 3px rgba(0, 0, 0, 0.2);

		flex-grow: 0;
		flex-shrink: 0;

		display: flex;
		justify-content: space-between;
	}

	&__controls {
		color: @notes-color-inverted;
		background: transparent;
		font-weight: bold;
	}

	&__controls-add {
		
		.state-locked {display: none;}
		.state-unlocked {display: block;}

		&.is-locked {
			.state-locked {display: block;}
			.state-unlocked {display: none;}
			cursor: pointer;
		}
	}

	&__controls-save {
		.icon {
			vertical-align: middle;
			&::before {
				font-size: 1.25em;
				display: inline;
			}
		}
	}
}