.mc-toc {
	
	overflow: hidden;

	&__inner {
		display: flex;
		height: 100%;
	}

	// List
	// --------------------------------------------------------------
	&__list {
		width: 50%;

		overflow: auto;
	}

	&__list-item {
		display: flex;
		cursor: pointer;
		padding-top: @item-padding/2;
		padding-bottom: @item-padding/2;

		background: @menu-item-color;

		// States
		// -----------------------------------------------------------
		.no-touch &:hover {
			background: @menu-item-color-hover;
		}

		&.is-active {
			background: @menu-item-color-active;
			font-weight: bold;
		}

		.no-touch &.is-active:hover {
			background: @menu-item-color-active-hover;
		}
	}

	&__list-item-num {
		//font-weight: bold;
		min-width: 2rem;
		flex-shrink: 0;

		text-shadow: 0.5px -0.5px currentColor; // Fake slight bolding.

		margin-right: @item-margin;
	}
	
	&__list-item-title {
		flex-grow: 1;
	}

	&__list-item-icon {
		flex-shrink: 0;
		margin-left: @item-margin;
	}

	&__list-heading {
		font-weight: bold;
		
		margin-top: @item-margin;
		border-top: none !important;
		cursor: auto;

		.no-touch &:hover {
			background: transparent;
		}
	}

	// Preview
	// --------------------------------------------------------------
	&__preview {
		padding: @padding-ver @padding-hoz;
		width: 50%;
	}

	&__preview-inner {
		display: flex;
		align-items: center;
		justify-content: center;

		height: 100%;
	}

	&__preview-item {

		width: 100%;

		// States
		// --------------------------------------
		display: none;
		&.is-active {
			display: block;
		}
	}

	&__preview-item-header {
		display: flex;
		align-items: center;
		min-height: 2rem;
		margin-bottom: @menu-subtitle-margin;
	}

	&__preview-item-title {
		flex-grow: 1;
		font-size: @subtitle-size;
	}

	&__preview-item-title-inner {
		display: flex;
	}
	&__preview-item-title-num {
		flex-grow: 0;
		flex-shrink: 0;
	}

	&__preview-item-icon {
		flex-shrink: 0;
		margin-left: @item-margin;

		.icon::before {
			font-size: 2rem;
		}
	}

	&__preview-item-graphic {
		position: relative;

		// Add a loading gif behind images while they load.
		// FIXME: This should ideally be inherited from main Hybrid (we have a helper for it!).
		@keyframes loadingGif {
            0% 		{ transform: rotate(0deg); }
            100% 	{ transform: rotate(360deg); }
        }

		&::before {
			content: "";
			position: absolute;
			width: 4rem;
			height: 4rem;
			top: calc(50% ~"-" 2rem);
			left: calc(50% ~"-" 2rem);
			background-image: url("assets/dd-loading-gif.png");
			animation: loadingGif 750ms 0s infinite normal steps(8, end) forwards;
			background-position: center;
			background-repeat: no-repeat;
			background-size: 100%;
			transform-origin: center;
		}
	}

	// Border applied to img as it will have unpredictable width/height due to its sizing restrictions.
	&__preview-item-img {
		border: 2px solid mix(@menu-color, @menu-color-inverted);
		border-radius: @border-radius;
		overflow: hidden;

		// Placeholder (if no image present).
		min-width: 50%;
		max-width: 100%;
		min-height: @hyb-device-height / 3;
		max-height: @hyb-device-height / 1.4;
		margin: auto;
		width: auto;

		//background: #DDDDDD; // Placeholder colour while the image loads.
	}
}