.progress {

	border-radius: @border-radius;

	&__inner {
		display: flex;
		width: 100%;
		height: 100%;
		align-items: center;

		padding-right: @item-margin; // Stylistic choice.
	}

	// Active/inactive states
	// ---------------------------------------------------------------------
	
	// When inactive, show only the button (not the bar).
	width: @progress-btn-width;
	background: @progress-color; // "Button" background color.

	.transition(width @duration*3 ease, background @duration*3 ease;);

	&__bar {
		opacity: 0;
		.transition(opacity @duration*3 ease);

		// Improves performance by ignoring mouse-overs on the bar when it's in its closing transition.
		pointer-events: none; 
	}

	&.is-active {
		width: 100%;
		background: transparent;
	}

	&.is-active &__bar {
		opacity: 1;
		pointer-events: all;
	}

	// Button
	// ---------------------------------------------------------------------
	&__btn {
		width: @progress-btn-width;
		padding: @item-padding;

		flex-grow: 0;
		flex-shrink: 0;

		background: transparent;
		color: @progress-color-inverted;
	}

	// If there's only one progressView in the footer, don't display its button.
	&:only-child .progress__btn {
		display: none;
	}

	// Bar
	// ---------------------------------------------------------------------
	&__bar {
		position: relative;
		width: 100%;
		height: 100%;
		cursor: pointer;

		//.transition(all @duration ease);
	}
	
	&__bar-inner {
		position: absolute;
		left: 0;
		right: 0;

		display: flex;
		align-items: center;
		//overflow: hidden;
		height: 100%;
	}

	&__bar-item {
		position: relative;

		background: linear-gradient(to bottom, @progress-color 40%, @progress-color);

		height: @progress-bar-size;
		flex-grow: 1;
		.transition(all @duration ease);

		// Extend the vertical mouseover detection of the item.
		&::after {
			content: "";
			position: absolute;
			height: @progress-bar-icon-size;
			top: 50%;
			transform: translateY(-50%);
			left: 0;
			right: 0;
		}

		// States
		// ------------------------------------
		&.is-complete {
			background: linear-gradient(to bottom, #4ad50e 40%, #3daf0b);
		}
		
		// Icon
		// ------------------------------------
		&.has-icon {
			
			margin-left: -2px;
			margin-right: -2px;
			width: @progress-bar-icon-size;
			height: @progress-bar-icon-size;
			border: 3px solid @progress-color;
			color: @progress-color;
			background: @footer-color;
			border-radius: 50%;

			flex-grow: 0;
			flex-shrink: 0;

			display: flex;
			align-items: center;
			justify-content: center;

			z-index: 1;

			.icon {
				margin-top: 2px;
			}
			.icon::before {
				font-size: @progress-bar-icon-size/2;
			}

			// States
			// ---------------------------------------------
			&.is-active {
				border-color: #0c3857;
				color: #0c3857;
			}

			&.is-complete {
				border-color: #4ad50e;
				color: #4ad50e;
			}

			&.is-complete.is-active {
				border-color: #297508;
				color: #297508;
			}
		}
	}

	// Reduce icon sizes in small theme.
	&__bar.theme-small &__bar-item.has-icon {
		@a: 0.75;
		width: @progress-bar-icon-size*@a;
		height: @progress-bar-icon-size*@a;
		border-width: 2px;
		.icon::before {
			font-size: @progress-bar-icon-size / 2 * @a;
		}
	}

	// Moves around to indicate which item the user is currently viewing.
	&__bar-cur {
		position: absolute;
		pointer-events: none; // The cur is purely visual. It should not block clicks to the item behind it.
		
		left: 0;
		top: 50%;
		transform: translateY(-50%);

		height: @progress-bar-size;

		width: 20px;

		background: linear-gradient(#0c3857, #072133);
		box-shadow: -1px 0px 0px @footer-color, 3px 0px 0px @footer-color;
		.transition(all @duration ease);

		&.is-complete {
			background: linear-gradient(#297508, #1b4f05);
		}		
	}

	// Preview
	// ---------------------------------------------------------------------
	&__preview {
		width: @progress-preview-width;

		position: absolute;
		pointer-events: none;
		opacity: 0; // Hidden unless active hovered.
		z-index: 2;
		display: block;

		.transition(@duration*2 opacity ease);

		&.is-active {
			opacity: 1;
		}
	}

	&__preview-inner {
		
		// Transform the preview to be above the item.
		//border-radius: @border-radius;
		border: @progress-preview-color 1px solid;

		transform: translateX(-25%) translateY(calc(-100% ~"-" (@progress-preview-tail-size*1.5)));

		filter: drop-shadow(0 2px 4px rgba(0,0,0,0.25));
	}

	// Only one item is ever shown at a time.
	&__preview-item {
		display: none;

		&.is-active {
			display: block;
		}
	}

	&__preview-graphic {
		width: 100%;
		min-height: @progress-preview-width * 0.5; // Show an empty placeholder while the image loads.
		background: white;
	}

	&__preview-img {
		width: 100%;
	}

	&__preview-title {
		
		position: relative;
		display: block;
		padding: @progress-preview-padding;

		font-size: @progress-preview-title-size;

		background: @progress-preview-color;
		color: @progress-preview-color-inverted;

		// Tail
		&::after {
			content: "";
			position: absolute;
			
			bottom: 0;
			left: 25%;
			width: 0;
			height: 0;
			border: @progress-preview-tail-size solid transparent;
			border-top-color: @progress-preview-color;
			border-bottom: 0;
			margin-left: -@progress-preview-tail-size;
			margin-bottom: -@progress-preview-tail-size;
			transition-timing-function: scaleX(0.75);
		}
	}

	&__preview-title-num {
		font-weight: bold;
		display: inline-block;
	}
}