.footer {
	
	color: @footer-color-inverted;
	background: @footer-color;
	z-index: 1;

	padding: @padding-ver @padding-hoz;

	box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.4);

	&__inner {
		display: flex;
		gap: @padding-hoz;
	}

	// Touch device fix:
	// Prevent the touch device zooming in from doubletaps on the footer.
	touch-action: manipulation;

	// Progress
	// -------------------------------------------------------
	&__progress {
		flex-grow: 1;
		display: flex;
		gap: @item-margin;
	}


	// Controls
	// -------------------------------------------------------
	&__controls-container {

		gap: @padding-hoz/2;

		flex-grow: 0;
		display: flex;
		align-items: center;
	}

	&__controls {
		background-color: transparent;
		color: inherit;

		.icon::before {
			font-size: 2.5rem;
		}

		&.is-locked {
			opacity: 0.5;
		}
	}


	// Label
	// -------------------------------------------------------
	&__label {
		position: relative;
		font-size: 1.25rem;

		// The following width settings allow for numbers as big as "100 / 100" to fit and not wrap.
		width: 5.5rem;
		margin-left: -0.5rem;
		margin-right: -0.5rem;

		text-align: center;
		white-space: nowrap;
	}

	// Shows the true block "_id" (for debug only).
	&__label-debug {
		position: absolute;
		inset: 0;
		font-size: 1rem;

		display: flex;
		align-items: center;
		justify-content: center;

		background: #880088;
		color: white;
		.transition(all @duration*2 ease);

		&:active {
			background: #88008825;
			color: transparent;
		}

		.u-no-select;
	}

	// Notes
	&__controls-notes {
		.icon-hm-notes {display: block;}
		.icon-hm-notes-off {display: none;}

		&.is-active {
			.icon-hm-notes {display: none;}
			.icon-hm-notes-off {display: block;}
		}
	}

	// Fullscreen
	&__controls-fs {
		.icon-video-fullscreen {display: block;}
		.icon-video-exit-fullscreen {display: none;}

		&.is-active {
			.icon-video-fullscreen {display: none;}
			.icon-video-exit-fullscreen {display: block;}
		}

		.icon::before {
			font-size: 2.75rem;
			margin: -0.125rem;
		}
	}
}