// Menu
// -----------------------------------------------------------------------------------

.menu {
	
	position: absolute;
	top: 0;
	left: 0;
	width: @hyb-device-width;
	height: @hyb-device-height;

	background: @menu-color;

	&__inner {
		display: flex;
		height: 100%;
	}

	// Show/hide
	// ---------------------------------------------------------
	pointer-events: none;
	transform: translateY(-102%);
	.transition(transform @duration*3 ease);
	&.is-active {
		pointer-events: all;
		transform: translateY(0);
	}


	// Nav
	// ---------------------------------------------------------
	&__nav {
		height: 100%;
		width: @menu-nav-size;
		background: @menu-nav-color;

		flex-shrink: 0;
		flex-grow: 0;

		display: flex;
		flex-direction: column;
	}

	&__nav-items {
		flex-grow: 1;
	}

	&__nav-btn {
		display: flex;
		align-items: center;
		justify-content: flex-start;
		width: 100%;
		height: @menu-nav-item-height;

		font-size: @btn-size;
		padding: @btn-padding;

		color: @menu-nav-color-inverted;
		background: @menu-nav-color;

		.icon {
			width: 30%;
			text-align: center;
			flex-grow: 0;
			flex-shrink: 0;

			color: @menu-nav-color-inverted2;
		}

		.icon::before {
			font-size: inherit;
		}

		// States
		// -------------------------------------
		.no-touch &:hover {
			background: darken(@menu-nav-color, 5%);
		}

		&.is-active {
			background: @menu-color;
			color: @menu-color-inverted;

			.icon {
				color: @menu-color-inverted;
			}

			.no-touch &:hover {
				background: darken(@menu-color, 5%);
			}
		}
	}
	
	// Content
	// ---------------------------------------------------------
	&__content {
		height: 100%;
		flex-grow: 1;

		padding: 0 @padding-ver;
	}
	
	
	// Copyright
	// ---------------------------------------------------------
	&__copyright {
		padding: @menu-nav-padding;

		font-size: 0.875rem;
		color: fade(@menu-nav-color-inverted, 70%);
	}
}